  </div>
</template>

<script>
import news_wraper from './components/new_wraper'

export default {
  name: 'app',
  data(){
    return {}
  },
  computed:{
    cats(){
      return this.$store.state.cats
    }
  },
  components: { news_wraper }
}
</script>

