<template>
<div id="t" class="col-md-3">
<div id='wraper'>
	<div id="select">
		<select class="form-control" v-model='c_cat'>
		  <option v-for='c in cats'>{{c}}</option>
		</select>
	</div>
	<ul>
		<div id="item-box" v-for='n in c_news[this.c_cat]'>
			<div id="item-name"><a :href="n.url" target="__blank">{{n.title}}</a></div>
			<div id="item-intro" v-if='n.intro'>{{n.intro}}</div>
			<div id="item-time" v-if='n.time'>{{n.time}}</div>
		</div>
	</ul>
</div>
</div>
</template>

<script>
	export default {
		data(){
			return {
				c_news: {},
				c_cat: '',
			}
		},
		props: ['cat'],
		mounted: function(){
			this.c_cat = this.cat
			this.c_id = this.cn_id

		},
		computed:{
			cats(){
				return this.$store.state.cats
			},
			news(){
				// this.cnews = this.$store.state.news
				return this.$store.state.news
			}
		},
		watch: {
			c_cat(){
				this.$store.dispatch('getNews',{'cat': this.c_cat})
				this.c_news = this.$store.state.news
			}
		}


	}
</script>

<style>
div #wraper {
	height: 100%;
	overflow-y: auto;
}
div #t {
	height: 88%;
	background-color: #e2e4e6;
	padding: 0;
	margin-top: 40px;
	border-radius: 5px
}

#item-box {
	width: 90%;
	background-color: #fff;
	margin: 0 20px 10px;
	border-radius: 5px;
	box-shadow:1px 1px 1px rgba(0,0,0,.3)
}

#item-box:hover {
	background-color: #f7f7f7;
}

ul {
	padding: 0;
	margin-top: 10px
}

#item-name {
	border-bottom: 1px solid #dbdbdb;
	padding: 5px 10px
}

#item-intro {
	padding: 10px;
	border-bottom: 1px solid #dbdbdb;
}

#item-time {
	padding: 5px 10px
}

a:hover {
	text-decoration: none;
}

#select {
	margin: 10px 20px 10px;
	width: 100px
}
</style>



